import MyAxios from "../../myAxios.js" // 引入axios
var app=getApp();     // 取得全局App
export default{
	data:()=>{
		return{
			address:{
				consignee:"",
				mobile:"",
				address:"点击选择地址",
				house_number:"",
				is_default:1,
				user_id:"",
			},
		}
	},
	onLoad() {
		this.$set(this.address,'user_id',app.globalData.user_id)
	},
	//点击分享
	onShareAppMessage:function(res){
		return {
			title: '约工宝专业约工---快速上门，全城服务',
			path: '/pages/index/index',
			imageUrl:"../../static/home/logo.png"
		}
	},
	methods:{
		//跳转页面
		go:function(url){
			uni.navigateTo({url})
		},
		//地图选点
		map:function(){
			//查询用户之前有没有允许或拒绝获取地理位置 如过以前没有拒绝和允许
			//第一次会询问 拒绝走fail 同意走success
			uni.authorize({
				scope:"scope.userLocation",
				//以前允许过 直接走这个
				success:()=>{
					uni.chooseLocation({
						success:(res)=>{
							this.address.address = res.name
						},
					})
				},
				//以前拒绝过  直接走这个
				fail() {
					//提示让用户允许授权
					uni.showModal({
						title:"提示",
						content:"请允许获取您的地理位置",
						showCancel:false,
						success: () => {
							uni.openSetting({});
						}
					})
				}
			})
		},
		//修改地址信息
		changeAddress:function(e,index){
			if(index == 1){       //修改联系人
				this.address.consignee = e.target.value
			}else if(index== 2){  //修改手机号码
				this.address.mobile = e.target.value
			}else if(index == 3){ //修改门牌号
				this.address.house_number = e.target.value
			}
		},
		//保存地址
		save:async function(){
			//手机号码正则
			if(!(/^1[3456789]\d{9}$/.test(this.address.mobile))){
			    uni.showModal({
			    	title:"提示",
			    	content:"手机号码有误，请重新输入",
			    	showCancel:false,
			    })
			    return  
			} 
			//如果用户点击保存时的数据有空的 就不能添加
			for(let key in this.address){
				if(this.address[key].toString() == "" || this.address.address.toString() == "点击选择地址"){
					uni.showModal({
						title:"提示",
						content:"地址信息有误",
						showCancel:false,
					})
					return
				}
			}
			//发送地址信息
			let result = await MyAxios.axios("https://ygbao.magicxhx.com/api/index/add_address",this.address,"post")
			//添加成功 刷新列表
			if(result[1].data.code == 1){
				uni.showToast({
					title:"地址添加成功",
					mask:true,
					complete() {
						setTimeout(()=>{
							uni.navigateBack()
						},1000)
					}
				})
			}else{
				uni.showModal({
					title:"提示",
					content:"地址添加失败",
					showCancel:false,
				})
			}
		}
	}
}
